<template>
  <div class="container">
    <nav class="level">
      <div class="level-left">
      </div>
      <div class="level-right">
        <div class="level-item">
          <p class="control" v-if="isLoggedin">
            <input class="input" v-model="searchTxt" type="text" placeholder="Search">
          </p>
          <p class="control" v-if="isLoggedin">
            <button class="button" @click="logout"> Logout
            </button>
          </p>

          <div class="field has-addons" v-if="!isLoggedin">
            <p class="control">
              <input class="input" v-model="mobile" type="text" placeholder="Enter Mobile Number">
            </p>

            <p class="control">
              <button class="button" @click="register">
                Request OTP
              </button>
            </p>            <p class="control">
              <input class="input" v-model="otp" type="text" placeholder="OTP">
            </p>
            <p class="control">
              <button class="button" @click="login">
                Login
              </button>
            </p>
          </div>
        </div>
      </div>
    </nav>
    <div class="columns">
      <div class="column">
        <HelloWorld v-if="me" :me="me" :searchTxt="searchTxt" />
        <h1 v-if="!isLoggedin" class="title">
          Please Login to see the available Shuttle/Taxi services...
        </h1>
        <a class="button is-link" href="https://drive.google.com/open?id=11aabJplFTFbHPnZn7OEEhkzrufS6pI7-">Android apk</a>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue';

const axios = require('axios');
const _ = require('lodash');

const instance = axios.create({
  baseURL: 'https://cloud-shuttle.herokuapp.com',
});
export default {
  name: 'home',
  components: {
    HelloWorld,
  },
  data() {
    return {
      searchTxt: '',
      mobile: '',
      otp: '',
      user: null,
      cabs: null,
    };
  },
  async created() {
    this.user = JSON.parse(localStorage.getItem('user'));
  },
  computed: {
    isLoggedin() {
      return _.get(this.user, 'token', false);
    },
    me() {
      return this.user;
    },
  },
  methods: {
    async register() {
      try {
        await instance.post('/users/register', { mobile: this.mobile, name: this.mobile });
        // this.user = data.data;
      } catch (e) {
        // console.log(e);
        // eslint-disable-next-line
        alert(e.message);
      }
    },
    async login() {
      try {
        const data = await instance.post('/users/login', { mobile: this.mobile, otp: this.otp });
        this.user = data.data;
        localStorage.setItem('user', JSON.stringify(this.user));
      } catch (e) {
        // console.log(e);
        // eslint-disable-next-line
        alert(e.message);
      }
      // console.log(this.user.data);
    },
    logout() {
      this.user = null;
      localStorage.setItem('user', null);
    },
  },
};
</script>
